@import '/src/variables.scss';

.profile__form {
  width: 100%;
}

.profile__formWrapper {
  width: 100%;
  padding: 12px 20px;
  display: flex;
  flex-direction: column;
  row-gap: 10px;
  background: $basic-white;
  border: 1px solid $color-grey-2;
  box-sizing: border-box;
  border-radius: 8px;
  margin-bottom: 10px;
}

.profile__formWrapper label {
  display: flex;
  flex-direction: column;
  row-gap: 3px;
  font-size: $font-size-4;
  line-height: $line-height-4;
}

.profile__formWrapper input {
  width: 50%;
  background: $basic-white;
  border: 1px solid $color-grey-3;
  box-sizing: border-box;
  border-radius: 5px;
  padding: 5px 10px;

  &:read-only {
    color: rgba(0, 0, 0, 0.3);
  }
}

.profile__textareaLabel {
  width: 100%;
}

.profile__textarea {
  background: $basic-white;
  border: 1px solid $color-grey-3;
  box-sizing: border-box;
  border-radius: 5px;
  padding: 5px 10px;
  min-height: 55px;
  width: 100%;
  resize: none;
}

.profile__formButton {
  margin-left: auto;
  display: block;
  text-align: center;
  padding: 5px 15px;
  align-self: flex-end;
  justify-self: flex-end;
  color: $basic-white;
  font-size: $font-size-2;
  line-height: $line-height-2;
  background-color: #52cf4f;
  border-radius: 5px;
  border: none;

  &:disabled {
    background-color: $color-grey-4;
  }
}

.invalid {
  outline: 1px solid #d91313;
}
